.our-program {
    background: #111518;
    padding-top: 40px;
    padding-bottom: 60px;
    margin-bottom: -3px;
    // margin-bottom: -200px;
    // overflow-x: hidden;

    @media screen and (min-width: 768px) {
        padding-top: 60px;
        padding-bottom: 125px;
    }

    @media screen and (min-width: 1680px) {
        padding-top: 80px;
        padding-bottom: 210px;
    }
}

// h2,
.program-title {
    margin-bottom: 40px;
    font-weight: 800;
    font-size: 20px;
    text-align: center;
    line-height: 130%;
    letter-spacing: 0.03em;
    color: #ffffff;

    @media screen and (min-width: 768px) {
        font-size: 26px;
        text-align: left;
    }

    @media screen and (min-width: 1680px) {
        margin-bottom: 88px;
        font-size: 46px;
    }
}

.timeline-block {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media screen and (min-width: 480px) {
        margin-left: auto;
        margin-right: auto;
        padding: 0 40px;
    }

    @media screen and (min-width: 768px) {
        padding: unset;
    }

    @media screen and (min-width: 1680px) {
        border-top: 1px solid #ffffff;
        max-width: 100%;
        margin-left: auto;
        margin-right: auto;
    }
}

.timeline {
    position: relative;
    width: calc(100% - 90px);
    margin-left: auto;
    margin-right: auto;
    margin-top: 5px;

    @media screen and (min-width: 480px) {
        width: calc(480px - 30px);
    }

    @media screen and (min-width: 768px) {
        max-width: calc(768px - 30px);
        margin-left: 88px;
        flex-direction: column;

        &::before {
            left: -112px;
        }
    }

    @media screen and (min-width: 1680px) {
        padding-top: 48px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
        max-width: calc(1680px - 30px);
        margin-left: auto;
        margin-right: auto;
        padding-left: 140px;
        padding-right: 140px;

        &::before {
            content: unset;
        }
    }

    li {
        list-style: none;
    }
}

.timeline__block {
    padding-bottom: 44px;
    border-left: 1px solid #ffffff;
    padding-left: 27px;
    margin-right: -20px;

    &::before {
        content: '';
        position: absolute;
        width: 12px;
        height: 12px;
        background: #ff6b09;
        border-radius: 50%;
        left: -5px;
        margin-top: -1px;
        z-index: 2;
    }

    &:last-child {
        border-left: 1px solid transparent;
    }

    @media screen and (min-width: 768px) {
        padding-left: 112px;
        margin-right: -176px;
        padding-bottom: 8px;

        &::before {
            content: '';
            position: absolute;
            width: 12px;
            height: 12px;
            background: #ff6b09;
            border-radius: 50%;
            left: -6px;
        }
    }

    @media screen and (min-width: 1680px) {
        flex: 0 0 calc(100% / 5 - 160px / 5);
        position: relative;
        border: transparent;

        padding-left: unset;
        margin-right: unset;

        &::before {
            margin-top: 0;
            left: 0;
            content: '';
            position: absolute;
            width: 16px;
            height: 16px;
            background: #ff6b09;
            border-radius: 50%;
            top: -62px;
        }
    }
}

@media screen and (max-width: 480px) {
    .timeline__block {
        min-width: 176px;
    }
}

.semester__number {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 28px;
    letter-spacing: 0.08em;
    color: #ffffff;
    text-transform: uppercase;
    max-width: 100%;
    position: relative;
    top: -6px;

    @media screen and (min-width: 768px) {
        font-size: 14px;
    }

    @media screen and (min-width: 1680px) {
        font-size: 30px;
        line-height: 28px;
        text-transform: none;

        .sem {
            font-size: 20px;
        }
    }
}

.semester__items {
    @media screen and (min-width: 768px) {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
    }

    @media screen and (min-width: 1680px) {
        flex-direction: column;
    }
}

.semester__item {
    margin-bottom: 4px;

    @media screen and (min-width: 768px) {
        flex: 0 0 calc(100% / 3 - 80px / 3);
        margin-bottom: 40px;
        margin-right: 40px;
    }

    @media screen and (min-width: 1680px) {
        margin-top: 12px;
        margin-bottom: 6px;
        margin-right: 0;
    }
}

.semester__title {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    line-height: 164.9%;
    display: inline-flex;
    align-items: center;
    letter-spacing: 0.08em;
    color: #ff6b09;
    font-size: 12px;
    padding-top: 4px;
    padding-bottom: 7px;

    @media screen and (min-width: 768px) {
        font-size: 12px;
        padding-bottom: 0;
    }

    @media screen and (min-width: 1680px) {
        font-size: 20px;
    }
}

.semester__description {
    margin-top: 4px;

    @media screen and (min-width: 768px) {
        margin-top: 8px;
    }

    li {
        font-family: 'Montserrat', sans-serif;
        font-style: normal;
        font-weight: 500;
        letter-spacing: 0.08em;
        color: #b9b9b9;
        opacity: 0.5;
        font-size: 12px;
        line-height: 20px;

        @media screen and (min-width: 768px) {}

        @media screen and (min-width: 1680px) {
            font-size: 18px;
            line-height: 28px;
        }
    }
}

.timeline-block::after {
    content: '';
    width: 176px;
    height: 137px;
    position: absolute;
    z-index: 3;
    transform: translate(50%, 490%);
    // left: 170px;
    background-image: url(/images/program-img-mob-min.png);
    background-repeat: no-repeat;
    background-size: cover;
    display: block;
    overflow: hidden;

    @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
        background-image: url(/images/program-img-mob@2x-min.png);
    }

    @media screen and (min-width: 768px) {
        width: 368px;
        height: 268px;
        transform: translate(54%, 135%);
        background-image: url(/images/program-img-tab-min.png);

        @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
            background-image: url(/images/program-img-tab@2x-min.png);
        }
    }

    @media screen and (min-width: 1680px) {
        width: 772px;
        height: 510px;
        transform: translate(-70%, 80%);
        background-image: url(/images/program-img-desk-min.png);

        @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
            background-image: url(/images/program-img-desk@2x-min.png);
        }
    }
}

